import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { FunctionComponent, useRef, useEffect, useState } from 'react';
import { CreateClassName } from '../../utils/Utils';

type MessagesProps = {
    audio: any
    duration: number
}
 
let interval:any;

export const AudioPlayer: FunctionComponent<MessagesProps> = ({audio, duration}) => {
    const videoRef = useRef(null);
    const [playing, setPlaying] = useState(false);
    const [time, setTime] = useState(duration);

    function formatTime(val: number){
        const displayTime = Math.round(val / 1000);
        const minutes = ("0" + Math.round(displayTime / 60)).slice(-2);
        const secondes = ("0" + Math.round(displayTime % 60)).slice(-2);
        return minutes + ":" + secondes
    }

    function play(){
        if(!videoRef) return;

        if(!playing){
            videoRef.current.play();
            setPlaying(true);
            if(time === duration) setTime(0);
            interval = setInterval( () => {
                setPlaying(playing => {
                    if(!playing){
                        clearInterval(interval);
                        setTime(duration);
                    } else {
                        setTime(time => time + 1000);
                    }
                    return playing;
                });
                
            }, 1000)

        } else {
            videoRef.current.pause();
            setPlaying(false);
            clearInterval(interval);
        }
    }

    return  <div className="audio-player">
                <div className="duration">{formatTime(time)}</div>
                <FontAwesomeIcon icon={playing ? "pause" : "play"} onClick={play}/>
                <video ref={videoRef} src={audio} onEnded={ e => {
                    setPlaying(false);
                    setTime(duration);
                }}/>
            </div>
}