import { faUserSecret } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useEffect, useState } from "react";
import { DEFAULT_ROOM__ID } from "../../../server/models/src/namespace";
import { Room, RoomOptions } from "../../../server/models/src/room";
import { User } from "../../../server/models/src/user";
import { CheckBox } from "../../components/Input/CheckBox";
import { TextInput } from "../../components/Input/TextInput";
import { Modal } from "../../components/Modal/Modal";
import { CreateClassName } from "../../utils/Utils";

type LobbyProps = {
    display: boolean
    rooms: Room[]
    socketUser: User
    onJoinRoom: (room:Room) => void
    onCreateRoom: (room:Room) => void
    onLeaveRoom: (room:Room) => void
    unReadMessages: {string?: number}
}

let clickTimeout;

export const LobbyScreen = ({display, rooms, socketUser, onJoinRoom, onCreateRoom, onLeaveRoom, unReadMessages}:LobbyProps) => {
    const [animation, setAnimation] = useState("");
    const [currentRoom, setCurrentRoom] = useState(DEFAULT_ROOM__ID);
    const [modal, setModal] = useState("");
    const defaultRoom:RoomOptions = {private: false, owner: socketUser, id: null, name: null, allowedUsers: []};
    const [newRoom, setNewRoom] = useState(defaultRoom);

    useEffect(() => {
        setAnimation(display ? "animate__slideInLeft" : "animate__slideOutLeft")
        if(!display) setTimeout(() => setAnimation(""), 200);
    }, [display]);

    const screenUsersClassName = CreateClassName({
        "animate__animated": true,
        "hidden": "",
        [animation]: animation
    }, "screen screen-lobby");

    if(!animation) return null;

    const roomSelected = rooms.filter(room => room.id === currentRoom)[0];

    const chatWithUserExist = (username) => {
        let exist = false;
        rooms.filter(room => room.id.includes(username) && room.id.includes(socketUser.name)).forEach(room => {
            exist = !!room.getUsers({name: socketUser.name}).length
        })
        return exist;
    }

    const getUserList = () =>{
        const defaultRoom = rooms.filter(room => room.id === DEFAULT_ROOM__ID)[0];
        if(defaultRoom) [];
        return defaultRoom.users
    }
    
    return <div className={screenUsersClassName}>
                <h4><FontAwesomeIcon icon="users"/>Rooms<FontAwesomeIcon icon="plus" onClick={e => setModal("open")}/></h4>
                <ul className="rooms">
                    {rooms.map(room =>  {
                        const connected = !!room.getUsers({name: socketUser.name}).length;
                        const selected = room.id === currentRoom;
                        return <li className={ CreateClassName({selected, connected})} onClick={e => setCurrentRoom(room.id)}>
                                    <span   onMouseDown={e => clickTimeout = setTimeout(() => onJoinRoom(room), 300)} 
                                            onMouseUp={e => clickTimeout && clearTimeout(clickTimeout)}>
                                                {room.getCleanName(socketUser.name)}{room.private ? <FontAwesomeIcon icon="lock"/> : null}
                                                <span className="unread">
                                                    {unReadMessages[room.id] ? <FontAwesomeIcon icon="comment"/> : null}
                                                </span>
                                    </span>
                                    <span className="info">
                                        {room.users.length}
                                        <FontAwesomeIcon icon="user"/> 
                                    </span>
                                    <span className="action">
                                        {connected && <FontAwesomeIcon    icon="times" 
                                                            className={room.id !== DEFAULT_ROOM__ID ? "" : "hidden"}
                                                            onClick={e => {
                                                                onLeaveRoom(room)
                                                                if(room.id === currentRoom) setCurrentRoom(DEFAULT_ROOM__ID);
                                                            }}/>}
                                        {!connected && <FontAwesomeIcon    icon="sign-in-alt" 
                                                            className={room.id !== DEFAULT_ROOM__ID ? "" : "hidden"}
                                                            onDoubleClick={e => {
                                                                onJoinRoom(room)
                                                            }}/>}
                                    </span>
                                </li>
                            })}
                </ul>
                <h4><FontAwesomeIcon icon="user"/>Users</h4>
                <ul className="users">
                    {roomSelected && roomSelected.users.filter(user => user.name !== socketUser.name).map(user =>  <li>
                                            <span>{user.name}</span>
                                            <span>
                                                <FontAwesomeIcon    icon="envelope" 
                                                                    onDoubleClick={e => onCreateRoom(new Room({
                                                                        id: socketUser.name + ":" + user.name,
                                                                        name: socketUser.name + "-" + user.name,
                                                                        private: true,
                                                                        owner: socketUser,
                                                                        allowedUsers: [user.name]
                                                                    }))}
                                                                    className={chatWithUserExist(user.name) ? "hidden" : ""}/>
                                            </span>
                                        </li>)}
                    {roomSelected && roomSelected.allowedUsers.filter(name => !roomSelected.users.filter(user => user.name === name).length && name !== socketUser.name).map(username => <li className="inactive">
                                                                                                                                                <span>{username}</span>
                                                                                                                                            </li>)}
                </ul>
                {!!modal && <Modal type="confirm" className={modal} onConfirm={() => {
                                                                    onCreateRoom(new Room(newRoom));
                                                                    setModal("")
                                                                    setNewRoom(defaultRoom)
                                                                }} onClose={() => {
                                                                        setModal("")
                                                                        setNewRoom(defaultRoom)
                                                                }}>
                                <div className="modal-content-create">
                                    <h3>Create Room</h3>
                                    <TextInput 
                                        initialValue={""}
                                        placeholder="Nom"
                                        onChange={name => setNewRoom({...newRoom, name})}/>
                                    <div className="private-options">
                                        <CheckBox 
                                            placeholder="Private"
                                            initialValue={false}
                                            onChange={priv => setNewRoom({...newRoom, private: priv})}/>
                                        <div className={"user-list " + ( newRoom.private ? "" : "disabled")}>
                                            <h4><FontAwesomeIcon icon="user"/>Users</h4>
                                            <ul>
                                                {
                                                    getUserList().map(user => <li className={newRoom.allowedUsers.includes(user.name) ? "selected" : ""}
                                                                                onDoubleClick={e => setNewRoom({...newRoom, allowedUsers: newRoom.allowedUsers.includes(user.name) ? newRoom.allowedUsers.filter(name => name !== user.name) : [...newRoom.allowedUsers, user.name]})}>{user.name}</li>)
                                                }
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </Modal>}
            </div>
}